
.todo-content.card-content-inner{

    width:80%;
    height: 5rem;
}
.card-content.todo-card{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.card-content.todo-card .control-box{
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.finished{
    border:1px solid rgb(42, 218, 56);
}
.content{
    padding-bottom: 2.5rem;
}
nav.bar{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;-ms-flex-pack: distribute;justify-content: space-around;
    background: none;-webkit-box-align: center;-ms-flex-align: center;align-items: center;
    border: none;-webkit-box-shadow: none;box-shadow: none;
}
.circle{
    width: 2rem;height: 2rem;border-radius: 50%;text-align: center;line-height: 2rem;
    color: #fff;
    font-size: 10px;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.circle.all{
    background: #fff;
    color:rgb(21, 104, 212);
    border:1px solid rgb(21, 104, 212);
}
.circle.all.active{
    border:none;
    background: rgb(21, 104, 212);
    color: #fff;
}
.circle.unfinish{
    background: #fff;
    color:rgb(216, 39, 33);
    border:1px solid rgb(216, 39, 33);
}
.circle.unfinish.active{
    border:none;
    background: rgb(216, 39, 33);
    color: #fff;
}
.circle.finish{
    background: #fff;
    color:rgb(21, 212, 53);
    border:1px solid rgb(21, 212, 53);
}
.circle.finish.active{
    border:none;
    background: rgb(21, 212, 53);
    color: #fff;
}
.mask{
    position: absolute;
    width: 100%;
    height: 100vh;
    z-index: 999;
    background: rgba(0,0,0,0.2);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;-webkit-box-pack: center;-ms-flex-pack: center;justify-content: center;-webkit-box-align: center;-ms-flex-align: center;align-items: center;
}
.insert-box{
    width: 70%;
    height: 15rem;
    background: #fff;
    border-radius: 0.5rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;-webkit-box-orient: vertical;-webkit-box-direction: normal;-ms-flex-direction: column;flex-direction: column;-webkit-box-align: center;-ms-flex-align: center;align-items: center;-ms-flex-pack: distribute;justify-content: space-around;
}
.insert-box textarea{
    width: 80%;
    height: 50%;
    border: 1px solid rgb(224, 224, 224);
}
.insert-box .btn-box{
    width: 70%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;-ms-flex-pack: distribute;justify-content: space-around;
}
.card.normal .todo-card{
    width: 100%;
}
.card.normal .check-box{
    display: none;
}
.select.card{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;-webkit-box-align: center;-ms-flex-align: center;align-items: center;
}
.select.card .todo-card{
    width: 90%;
}
.check-box{
    
    width: 10%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;-webkit-box-pack: center;-ms-flex-pack: center;justify-content: center;-webkit-box-align: center;-ms-flex-align: center;align-items: center;
}








































































































































































































































































































